/* eslint-disable no-console */
import axios from 'axios';

const GEMINI_API_KEY = process.env.NEXT_PUBLIC_GEMINI_API_KEY;
const API_URL =
  'https://generativelanguage.googleapis.com/v1beta/models/gemini-pro:generateContent';

export interface GeminiMessage {
  role: 'user' | 'model';
  content: string;
}

// Cache para respostas comuns e hist√≥rico de conversas
const messageCache = new Map<string, string>();
const conversationHistory: GeminiMessage[] = [];

const SYSTEM_PROMPT = `Voc√™ √© um assistente virtual especializado da Adriana Showroom, com foco em marketing e vendas no setor de moda. 
Suas respostas devem ser:
- Concisas e diretas (m√°ximo 2-3 frases)
- Focadas em moda, roupas e acess√≥rios, estrat√©gias de marketing e vendas
- Profissionais, amig√°veis e sempre relacionadas ao contexto do showroom

Conhecimentos espec√≠ficos:
- Hor√°rio de funcionamento: 9h √†s 18h
- Tipos de produtos: roupas femininas, acess√≥rios, cal√ßados
- Formas de pagamento: cart√£o, PIX, dinheiro
- Pol√≠tica de trocas: at√© 7 dias ap√≥s a compra
- Estrat√©gias de marketing digital: campanhas no Instagram, an√∫ncios pagos, promo√ß√µes e descontos
- T√©cnicas de engajamento de clientes: e-mail marketing, promo√ß√µes sazonais, parcerias
- Pesquisa de mercado: tend√™ncias de moda, concorr√™ncia e comportamento de compra
- Aconselhamento de vendas: como aumentar o ticket m√©dio, t√©cnicas de vendas eficazes, upsell e cross-sell`;

function normalizeQuestion(question: string): string {
  return question.toLowerCase().trim();
}

export async function sendMessageToGemini(message: string): Promise<string> {
  const normalizedMessage = normalizeQuestion(message);

  // Verifica cache
  if (messageCache.has(normalizedMessage)) {
    return messageCache.get(normalizedMessage)!;
  }

  // Adiciona mensagem ao hist√≥rico
  conversationHistory.push({ role: 'user', content: message });

  try {
    const response = await axios.post(`${API_URL}?key=${GEMINI_API_KEY}`, {
      contents: [
        {
          parts: [
            {
              text: `${SYSTEM_PROMPT}\n\nHist√≥rico: ${formatHistory()}\nUsu√°rio: ${message}\nAssistente:`,
            },
          ],
        },
      ],
      generationConfig: {
        temperature: 0.3,
        maxOutputTokens: 150,
        topP: 0.8,
        topK: 40,
      },
    });

    let aiResponse = response.data.candidates[0].content.parts[0].text;

    // Adiciona resposta ao hist√≥rico
    conversationHistory.push({ role: 'model', content: aiResponse });

    // Armazena no cache se for uma pergunta comum
    if (shouldCacheResponse(normalizedMessage)) {
      messageCache.set(normalizedMessage, aiResponse);
    }

    return aiResponse;
  } catch (error) {
    console.error('Error calling Gemini API:', error);
    throw new Error('Falha ao obter resposta da IA');
  }
}

// Formata o hist√≥rico de conversas para melhorar contexto
function formatHistory(): string {
  return conversationHistory
    .slice(-5) // Mant√©m apenas as √∫ltimas 5 mensagens
    .map(
      (msg) =>
        `${msg.role === 'user' ? 'Usu√°rio' : 'Assistente'}: ${msg.content}`
    )
    .join('\n');
}

// Determina se uma pergunta deve ser cacheada
function shouldCacheResponse(message: string): boolean {
  const commonQuestions = [
    'hor√°rio',
    'funcionamento',
    'pagamento',
    'troca',
    'endere√ßo',
    'localiza√ß√£o',
    'pre√ßo',
    'desconto',
    'promo√ß√£o',
  ];

  return commonQuestions.some((q) => message.includes(q));
}
